import React, { useState } from 'react'
const MyComponent = () => {
    const [userImage, setuserImage] = useState("https://images.pexels.com/photos/3038740/pexels-photo-3038740.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260")
    const [ipresent, setIpresent] = useState(true);
    const [selection, setSelection] = useState(false);
    const [x, setX] = useState(0);
    const [y, setY] = useState(0);
    const [x1, setX1] = useState(0);
    const [y1, setY1] = useState(0);
    const input_func =(e)=>{
        var reader = new FileReader();
        reader.onload = function(eve){
            console.log(eve.target.result);
            setuserImage(eve.target.result);
            setIpresent(true);
        }
        reader.readAsDataURL(e.target.files[0]);
        setSelection(false);
    }
    const mdown = (e) => {
        setSelection(false);
        setX(e.nativeEvent.offsetX);
        setY(e.nativeEvent.offsetY);
        setX1(0);
        setY1(0);
    }
    const mup = (e) => {
        
        setX1(e.nativeEvent.offsetX - x);
        setY1(e.nativeEvent.offsetY - y);
        setSelection(true);
    }


    let container = {
        // border:"1px solid black",
        padding:"3rem",
        width:"100vw",
        height:"100vh",
        textAlign:"center",
    }
    let inputStyle = {
        // border:"1px solid black",
        display:"block",
        margin:"0 Auto",
        size:"1rem",
        
    }
    let wrapper =  { 
        border:"1px solid black", 
        display:"inline-block",
        margin:"1rem",
        padding:"0",
        boxShadow:
      `0 1px 1px hsl(0deg 0% 0% / 0.1),
      0 2px 2px hsl(0deg 0% 0% / 0.1),
      0 4px 4px hsl(0deg 0% 0% / 0.1),
      0 8px 8px hsl(0deg 0% 0% / 0.1),
      0 16px 16px hsl(0deg 0% 0% / 0.1)`,
    }

    return (
        <div style={container} >
            <div style={inputStyle}><label for="user_img"></label> <input type="file" onChange={(e)=>input_func(e)} id="user_img" accept="image/*" ></input></div>
            <div draggable="false" onMouseDown={(e)=>mdown(e)} onMouseUp={(e)=>mup(e)} style={wrapper}>
            {ipresent && 
            <div style={{position:"relative"}}>
            <img src={userImage} alt="imagedd" draggable="false" style={{maxHeight:"80vh",width:"auto", display:"block",}}></img>
            {selection && <div style={{position:"absolute", top:y , left:x, width:x1, height:y1, backgroundColor:"rgba(251, 251, 251, 0.493)",border:"1px solid black" }}></div>}
            </div>
            }
            </div>
            {ipresent && <div>
                <h3>-x: {x} , y: {y} ,height: {y1} ,width: {x1}</h3>
            </div>}
        </div>
    )
}

export default MyComponent
